.header {
	min-height: 100vh;
	/*for IE11*/
	min-height: calc(var(--vh, 1vh) * 100);
	background-image: url(https://drive.google.com/uc?export=view&id=1rZVg3bV-CFxvavnLTPvJ5HlIe0S_mbVn);
	/*↑ここまでが100vhの設定*/
	display: grid;
	place-items: center;
}

.header-title {
	background-color: #fff;
	display: inline-block;
	font-size: 3vw;
	padding: 1vw;
}