@charset "UTF-8";
body {
  font-family: "Noto Sans JP", "Helvetica Neue", "Segoe UI", "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  background: #fff;
  position: relative;
  color: #202020;
}

section {
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: aquamarine;
}
@media screen and (min-width: 768px) {
  section {
    background-color: brown;
  }
}

button {
  font-family: "Noto Sans JP", sans-serif;
}

/* Page Loader */
.js-loader::before {
  content: "";
  position: fixed;
  z-index: 110;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #707c8c;
  pointer-events: none;
  transition: all 0.5s ease-in;
}
.js-loader::after {
  content: "welcome";
  position: fixed;
  z-index: 110;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50px;
  pointer-events: none;
  font-size: 8vw;
  color: #fff;
  transition: all 0.4s ease-in;
}

.js-loader.is-loaded {
  position: unset;
}
.js-loader.is-loaded::before {
  opacity: 0;
  transition: all 0.5s ease-in;
  z-index: -1;
}
.js-loader.is-loaded::after {
  opacity: 0;
  transition: all 0.3s ease-in;
  z-index: -1;
}

.js-trigger {
  width: 100%;
  height: 500px;
  background: blue;
}

.js-fadein {
  display: inline-block;
  margin: 0 20px;
  height: 60px;
  width: 60px;
  background: #888;
  opacity: 0;
}

.header {
  min-height: 100vh;
  /*for IE11*/
  min-height: calc(var(--vh, 1vh) * 100);
  background-image: url(https://drive.google.com/uc?export=view&id=1rZVg3bV-CFxvavnLTPvJ5HlIe0S_mbVn);
  /*↑ここまでが100vhの設定*/
  display: grid;
  place-items: center;
}

.header-title {
  background-color: #fff;
  display: inline-block;
  font-size: 3vw;
  padding: 1vw;
}

